.stand {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #e8e59a;
}

.inputs {
  display: flex;
  flex-direction: column;
  gap: 48px;
  background-color: #79baec;
}
